@import url("https://fonts.googleapis.com/css2?family=Song+Myung&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap");

body {
  font-family :"'Gill Sans'";
  background-color: rgb(255, 255, 255);
  line-height: 1.5;
  font-weight: bolder;
  margin-left : 50px;
  margin-right : 50px;
}

header {
  text-align: center;
  font-size: 250px;
  margin-bottom: 50px;
}
header{cursor: pointer;}
header:hover .highlight:after{
  width:100%;
}
.highlight{
  display: inline-block; 
  position: relative;
}
.highlight:after{
  content:"";
  width: 0;
  height: 350px;
  display: inline-block;
  background: rgb(142, 200, 222);
  position: absolute;
  bottom:0;
  left:0;
  z-index:-1;
  transition: 0.2s all;
}
#index{
  font-size: 150px;
  margin-left : 500px; 
}
.container {
  display: grid;
  grid-template-columns: 450px 1fr;
}
nav {
  font-family :"'Gill Sans'";
  font-size: 75px;
  width: 10em;
}
main{
  font-family :"'Gill Sans'";
  margin-left: 40px;
  background-color: rgb(161, 182, 236);
  font-size: 70px;
}
footer {
  text-align: center;
  background-color: rgb(45, 68, 167); 
  color: white;
  font-size: 100px;
}
a:link, a:visited {
  background-color:rgb(130, 163, 255);
  padding: 10px 20px;
  text-decoration: none;
  display: block;
  font-size: 1.5em;
  margin-right: 260px;
  text-align: center;
}
a:hover, a:active { 
  background-color: #6d49e3; 
  color:white ;
}

button {
  font-family :"'Gill Sans'";
  font-weight: bolder;
  background-color: rgb(108, 125, 255);
  color: white ;
  padding: 10px 20px;
  margin: 20px; 
  font-size: 2em;
  cursor: pointer;
  transition-duration: 0.2s;
}
button:hover {
  background-color: rgb(0, 15, 148);
  color: white;
}
#Login input[type="text"] {
  width: 100%; 
  height: auto;
}
p {
  font-size: 50px;
}
